body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.material-button {
    padding: 8px;
    background: transparent;
    position:relative;
    overflow: hidden;
    border-radius: 10px;
    cursor: pointer;
}

.material-button .value {
    position: relative;
    z-index: 1;
}

.material-button .material {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: rgba(211, 211, 211, .2);
    position: absolute;
    z-index: 0;
    -webkit-animation: .8s bigger forwards;
            animation: .8s bigger forwards;
}

@-webkit-keyframes bigger {
    0% {
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    100% {
        -webkit-transform: scale(10);
                transform: scale(10);
    }
}

@keyframes bigger {
    0% {
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    100% {
        -webkit-transform: scale(10);
                transform: scale(10);
    }
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}


